export default {
  async asyncData({ $axios, i18n }) {
    let params = {};
    params.lang = i18n.locale == "en" ? "en" : i18n.locale;
    const { data } = (await $axios(`contacts`, { params })).data;
    return { data };
  },
  dataa: () => ({
    noMob: null,
  }),
};
